<template lang="pug">
  .form_container
    form(@submit.prevent="validateBeforeSubmit")
      fieldset
        legend.off_screen Formulario de contacto
        .field
          label(for="enterprise_1").tag.off_screen
            span Empresa
          .input
            input(type="text" name="enterprise" id="enterprise_1" placeholder="Empresa"
              v-model="enterprise"
              v-validate="'required'"
              :class="{'invalid': errors.has('enterprise') }"
              data-vv-as="Empresa")
            span(v-show="errors.has('enterprise')").help.alert {{ errors.first('enterprise') }}
        .field
          label(for="email_1").tag.off_screen
            span Correo electrónico
          .input
            input(type="email" name="email" id="email_1" placeholder="Correo"
              v-model="email"
              v-validate="'required|email'"
              :class="{'invalid': errors.has('email') }"
              data-vv-as="Correo")
            span(v-show="errors.has('email')").help.alert {{ errors.first('email') }}
        .field
          label(for="message_1").tag.off_screen
            span Mensaje
          .input
            textarea(name="message" id="message_1" placeholder="Mensaje"
              v-model="message"
              v-validate="'required'"
              :class="{'invalid': errors.has('message') }"
              data-vv-as="Mensaje")
            span(v-show="errors.has('message')").help.alert {{ errors.first('message') }}
      .actions
        button(type="submit" ref="submitButton").btn.btn--base.submit.flex_content-horizontal.jc_center
          span.text--uppercase.text--ls-sm Enviar Mensaje
    .form_event
      .feedback-loading.flex_content-horizontal.jc_center.ai_center
        i
        span.text--xs.text--uppercase.text--ls-sm Enviando
      .feedback-success.flex_content-horizontal.jc_center.ai_center
        i
        span.text--xs.text--uppercase.text--ls-sm Enviado
      .feedback-error.flex_content-horizontal.jc_center.ai_center
        i
        span.text--xs.text--uppercase.text--ls-sm Error
</template>

<script>
import aes256 from 'nodejs-aes256'
import crypto from 'crypto'
import axios from 'axios'

let config = {
  host    : 'smtp.webfaction.com',
  port    : 587,
  from    : '"Neodiko" <mail.sender@neodiko.com>',
  to      : 'contacto@lafragua.mx',
  subject : 'Formulario de Contacto - Website La Fragua',
  message : '',
  auth    : {
    user : 'mail_sender',
    pass : '4506lv5ms'
  }
}

export default {
  name: 'include_contactForm',
  data() {
    return {
      enterprise: "",
      email: "",
      message: ""
    }
  },
  methods: {
    cleanForm() {
      this.enterprise = ""
      this.email = ""
      this.message = ""
      this.$nextTick(() => this.$validator.reset())
      setTimeout(() => { this.$el.classList.remove("success") }, 3000)
    },
    encryptMessage(json) {
      let password        = crypto.randomBytes(Math.ceil(10 / 2)).toString('hex').slice(0, 50)
      let message         = JSON.stringify(json)
      let messageEncrypt  = aes256.encrypt(password, message)
      let passwordEncrypt = new Buffer(password).toString('base64')
      return messageEncrypt.concat('\n' , passwordEncrypt)
    },
    send() {
      this.$el.classList.add("loading")
      let json = config
      json.message = `Empresa: ${this.enterprise}<br/>Correo: ${this.email}<br/>Mensaje: ${this.message}`
      json = this.encryptMessage(json)
      axios({
        method:'post',
        url:'//api.neodiko.com/mail/sendMail',
        data: { code: json }
      }).then((response) => {
        this.$el.classList.remove("loading")
        this.$el.classList.add("success")
        this.cleanForm()
      }).catch((error) => {
        this.$el.classList.remove("loading")
        this.$el.classList.add("error")
        console.error(error)
      })
    },
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.send()
          return
        }
      }).catch((error) => console.error(error))
    }
  }
}
</script>
