import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/pages/Home'
import Services from '@/views/pages/Services'
import Projects from '@/views/pages/Projects'
import About from '@/views/pages/About'
import Contact from '@/views/pages/Contact'
import Project from '@/views/pages/Project'
import Service from '@/views/pages/Service'

Vue.use(Router)

const asyncScrolling = new Promise((resolve, reject) => {
  setTimeout(() => {
    resolve({ x: 0, y: 0 })
  }, 500)
})

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'page_home',
      component: Home
    },
    {
      path: '/servicios',
      name: 'page_services',
      component: Services,
      children: [
        {
          name: 'modal_service',
          path: ':pk/:name',
          component: Service,
          props: true
        }
      ]
    },
    {
      path: '/proyectos',
      name: 'page_projects',
      component: Projects
    },
    {
      path: '/proyecto/:pk/:name',
      name: 'landpage_project',
      component: Project,
      props: true
    },
    {
      path: '/nosotros',
      name: 'page_about',
      component: About
    },
    {
      path: '/contacto',
      name: 'page_contact',
      component: Contact
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if (to.name.indexOf('modal') == -1) {
      if (from.name.indexOf('modal') == -1) {
        return asyncScrolling
      } else {
        document.body.classList.remove('modal_active')
        if (to.name != 'page_services') {
          return asyncScrolling
        }
      }
    } else {
      document.body.classList.add('modal_active')
      if (from.name == 'page_home') {
        return asyncScrolling
      }
    }
  }
})
